:root {
  --bfl-primary: #c20430;
  --bfl-primary-hover: #970225;
  --bfl-text: #191919;
  --bfl-accent: #49cbff;
  --bfl-basic-white: #fff;
  --bfl-blue-tint: #5f98b6;
  --bfl-maize-tint: #b49056;
  --bfl-lavendar-tint: #7a5fab;
  --bfl-sage-tint: #7a5fab;
  --bfl-pink-tint: #9f5879;
  --bfl-sidewalk-tint: #818181;
  --bfl-rose-tint: #913131;
  --bfl-glare: #f1f1f1ff;
  --bfl-plaster: #d3d3d3ff;
  --bfl-dried-leaves: #674a2a;
  --bfl-concrete-tint: #353535;
  --bfl-ivory-tint: #fffff0ff;
  --bfl-primary-highlite: #ffebebff;
  --bfl-hazy-tint: #ffebebff;
  --bfl-grass-tint: #a6eca6;
}

main a {
  font-weight: 700;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.text-center {
  text-align: center;
}

.ml-1 {
  margin-left: 1rem;
}

.flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.unit-cost {
  margin-bottom: 0.5rem;
}
.unit-cost > .dollar-sign {
  font-weight: 500;
  font-size: 2rem;
}
.unit-cost > .value {
  font-size: 3rem;
  font-weight: 700;
}
.unit-cost > .day-cost,
.unit-cost .week-cost {
  font-size: 2rem;
  font-weight: 700;
}
.unit-cost > .check-prices {
  font-weight: 500;
  text-align: center;
  line-height: 1.2;
}
.unit-cost > .range {
  color: var(--bfl-concrete-tint);
}
.unit-cost > .plus-char {
  vertical-align: super;
  font-weight: 700;
}

.cost-extra {
  text-align: center;
}
.cost-extra p {
  padding-bottom: 0.5rem;
  font-style: italic;
}

.unit-dims {
  color: var(--bfl-concrete-tint);
}

.unit-extras {
  font-size: 0.9rem;
  line-height: 1.2;
  margin-top: 0.5rem;
}

.promo-badge {
  width: 4rem;
  height: auto;
  position: absolute;
  top: -1rem;
  left: -1rem;
  background: transparent;
}
.promo-badge svg {
  filter: drop-shadow(0 0 4px rgba(0, 0, 0, 0.4));
}

body :not(.woocommerce-cart) .main-content table tr th {
  background-color: #f5f5f5 !important;
}
body :not(.woocommerce-cart) .main-content table tr:nth-child(2n+1) {
  background-color: #eee !important;
}
body :not(.woocommerce-cart) .main-content table tr td {
  font-size: 1.2rem !important;
}

.wp-block-table table tr th {
  background-color: #f5f5f5;
}
.wp-block-table table tr:nth-child(2n+1) {
  background-color: #e0e0e0 !important;
}
.wp-block-table table tr td {
  font-size: 1.2rem !important;
}

#slide-out-widget-area .ocm-dropdown-arrow {
  transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  position: absolute;
  right: -6px;
  top: 10px;
  transform: translateY(-50%);
  cursor: pointer;
  height: 50px;
  width: 250px;
  line-height: 0;
}